
<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center mb-base">
      <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
    </div>
    <div id="loading-container" class="nv-row row hover vs-con-loading__container">
      <div class="nv-col md:w-1/2 w-full">
        <p class="info-row">
          <span>{{ $t('LG_TYPE') }}:</span>
          <span>{{ lg.typeName | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('LG_REFERENCE_NUMBER') }}:</span>
          <span>{{ lg.lgNumber | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('BENEFICIARY_NAME') }}:</span>
          <span>{{ lg.beneficiaryName | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('ISSUANCE_DATE') }}:</span>
          <span>{{ lg.issuanceDate | formatUnixDate }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('EXPIRY_DATE') }}:</span>
          <span>{{ lg.expiryDate | formatUnixDate }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('OPEN_ENDED') }}:</span>
          <span>{{ lg.auxiliary1 | prettyBool }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('EXPIRED') }}:</span>
          <span>{{ lg.auxiliary2 | prettyBool }}</span>
        </p>
      </div>
      <div class="nv-col md:w-1/2 w-full">
        <p class="info-row">
          <span>{{ $t('CURRENCY') }}:</span>
          <span>{{ lg.currencyAbbreviation | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('LG_AMOUNT') }}:</span>
          <span>{{ lg.lgAmount | currency('') | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('LG_AMOUNT_LOCAL') }}:</span>
          <span>{{ lg.lgAmountLocal | currency('') | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('MARGIN_CURRENCY') }}:</span>
          <span>{{ lg.marginCurrencyAbrreviation | currency('') | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('MARGIN_AMOUNT') }}:</span>
          <span>{{ lg.marginAmount | currency('') | placeholder('-') }}</span>
        </p>
      </div>
    </div>
  </nv-card>
</template>

<script>
import TradeFinanceService from '@/services/trade/TradeFinanceService'
import AlertMixin from '@/mixins/commons/AlertMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'

export default {
  name: 'lg-issued-view',
  mixins: [LoadingMixin, AlertMixin, DatePickerMixin],
  data: function () {
    return {
      lg: {}
    }
  },
  methods: {
    async load () {
      let self = this
      this.startLoading('loading-container')
      TradeFinanceService.issuedLgs({
        lgNumber: this.$route.params.id,
        fromDate: this.getTime(this.$route.params.fromDate),
        toDate: this.getTime(this.$route.params.toDate)
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.lg = response.data.lgList[0] || {}
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
